#app {
  display: grid;
  grid-template-areas: "header header" "aside main" "aside footer";
  grid-template-columns: minmax(200px, 264px) auto;
  grid-template-rows: auto auto auto;
  overflow: hidden;
}
body,
html {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  color: #060028;
}
.h1,
.h2,
.h3,
.h4 h1,
h2,
h3,
h4 {
  font-weight: 600;
}
.h1,
h1 {
  font-size: 60px;
}
.h3,
h3 {
  font-size: 44px;
}
.btn-primary {
  color: #FFFFFF;
  font-weight: 500;
  letter-spacing: 0.015em;
  line-height: 125%;
  background: #EB00FF;
  border: 1px solid #EB00FF;
  padding: 12px 27px;
  border-radius: 0.25rem;
  border-radius: 8px;
  transition: all 0.4s ease-in;
}
.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active {
  color: #FFFFFF;
  background: #F691FF;
  border: 1px solid #F691FF;
}
.btn-dark {
  font-weight: 500;
  line-height: 125%;
  letter-spacing: 0.015em;
  background: #2F0DFF;
  padding: 12px 27px;
  border-radius: 8px;
  border: 1px solid transparent;
  transition: all 0.4s ease-in;
}
.btn-dark:hover,
.btn-dark:focus,
.btn-dark:active {
  background: #2d0dff44;
  border: 1px solid transparent;
}
.btn-warning {
  line-height: 125%;
  letter-spacing: 0.015em;
}
.btn-outline-danger {
  color: #EB00FF;
  font-weight: 500;
  font-size: 16px;
  line-height: 106.8%;
  letter-spacing: 0.015em;
  border: 2px solid #EB00FF;
  transition: all 0.4s ease-in;
  background-color: transparent;
}
.btn-outline-danger:hover,
.btn-outline-danger:active,
.btn-outline-danger:focus {
  color: #fff;
  background-color: #EB00FF;
  border-color: #EB00FF;
}
::-webkit-scrollbar-thumb {
  width: 8px;
  height: 26px;
  background: #000;
  border-radius: 10px;
  background-color: #060028;
}
::-webkit-scrollbar {
  width: 10px;
  background-color: #f9f9fd8a;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #9d92d71d;
}
.main {
  grid-area: main;
  min-height: 100vh;
  position: relative;
}
.form-label {
  font-size: 0;
}
.br {
  border-right: 1px solid #fff;
}
@media (max-width: 992px) {
  #app {
    grid-template-areas: "header" "main" "footer";
    grid-template-columns: 100%;
    grid-template-rows: auto auto auto;
    position: relative;
  }
  .aside {
    position: absolute;
    top: 74px;
    left: 0;
    padding: 10px 20px;
    z-index: 35;
    transform: translateX(-100%);
    transition: all 0.6s ease-in;
    background: #fff;
  }
  .naw-show {
    transform: translateX(0);
  }
}
